.container_VidUpload{
    position: absolute;
    background-color: rgba(0,0,0,0.6);
    width: 100vw;
    height: 100vh;
    z-index: 300;
    display: flex;
}

.container2_VidUpload{
box-shadow: 2px 2px rgba(142,142,142,0.681),
-2px -2px rgba(142,142,142,0.681);
padding: 1rem;
width: fit-content;
height: fit-content;
background-color:  black;
color: rgb(136,133,129);
position: relative;
margin: auto;
display: flex;
flex-direction: column;
border-radius: 1rem;
}

.ibox_viduplaod{
    margin: 0.5rem;
    font-size: larger;
    border-radius: 5px;
    background-color: transparent;
    padding: 0.2rem;
    color: aliceblue;
    width: calc(100% - 2rem);
    justify-content: center;
}

.ibox_viduplaod_crop{
  margin: 0.5rem;
  font-size: large;
  border-radius: 3px;
  background-color: black;
  padding: 0.2rem;
  color: gray;
  display: grid;
}

.editVideo{
  margin-right: 2rem;
}

.ibox_viduplaod_name{
  margin: 0.5rem;
  margin-right: 0.12rem;
  font-size: medium;
  border-radius: 3px;
  background-color: black;
  padding: 0.3rem;
  color: gray;
  display:table-row;
}

.ibox_div_vidupload_segments{
flex-direction: row;
display: flex;
    align-items: center;
    background-color: transparent;
    width: 100%;
}
.ibox_div_vidupload{
    display: flex;
    align-items: center;
    background-color: transparent;
    flex-direction: column;
    width: 100%;

}

.btn_addPart{
  background-color: white;
  color: black;
  font-weight: bolder;
  border: 2p solid;
  width: 10rem;
}

.btn_vidupload{
    background-color: black;
    color: white;
    font-weight: bolder;
    border: 2p solid;
}

.btn_vidupload:hover{
    color: orange;
}

.loader{
    width: 7rem;
    height: 7rem;
    padding: 2rem 0;
    margin: auto;
}

